.block {
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;

  $colors:
    "1" rgb(100%, 0%, 0%),
    "2" rgb(80%, 0%, 0%),
    "3" rgb(60%, 0%, 0%),
    "4" rgb(40%, 0%, 0%),
    "5" rgb(20%, 0%, 0%),
    "6" rgb(0%, 0%, 0%);

  @each $name, $color in $colors {
    &--#{$name} {
      background-color: $color;
    }
  }
}
